.MainDashboard {
  display: grid;
  grid-template-rows: 50px 1fr;
  height: 100vh;
  overflow: hidden;
}
.MainDashboard section {
  padding-top: 5%;
  height: calc(100vh - 100px);
  overflow-y: auto;
}
.MainDashboard .smallMenu {
  color: black;
  position: relative;
  top: 2%;
  left: 5%;
}
.MainDashboard .Status-bar {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.Menu-Main {
  position: absolute;
  top: calc(50px + 2%);
  left: 2%;
  cursor: pointer;
}
.Drawer {
  grid-row: 2;
  display: grid;
  grid-template-columns: 50px 1fr;
  grid-gap: 2% 0%;
  padding: 20% 10%;
  /* Borde */
  width: 20vw;
  height: calc(100vh - 50px);
}

.Drawer h3 {
  font-weight: bold;
}
.Drawer h5 {
  grid-column: 2;
  cursor: pointer;
}
.Drawer span {
  grid-column: 2;
  color: #757575;
  cursor: pointer;
}

.MainDashboard .Header {
  grid-column: 1/-1;
  background: #e6c435;
  display: flex;
  padding: 0% 3%;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.MainDashboard .Header span {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-weight: bold;
  font-family: "Poppins";
  width: 20%;
}
.MainDashboard .Header span p {
  margin: 0px;
}
.MainDashboard .Header .message {
  position: relative;
  top: 2%;
}

.Drawer .on {
  color: #e6c435;
}
