.Proveedores {
  display: grid;
  grid-template-columns: 1fr 0.9fr;
}

.Proveedores .dashprove {
  width: 100%;
  height: 100vh;
}
.Prove-form {
  padding-top: 10%;
  padding-right: 10%;
  width: 90%;
  margin: 0% auto;
}
.Prove-form h1 {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 200%;
}
.Prove-form p {
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  color: #245880;
  font-size: 1.4em;
}
.Prove-form form {
  width: 80%;
}
.Prove-form form input {
  margin-bottom: 2%;
}
.Prove-form form .checkboxs {
  display: flex;
  flex-direction: column;
}

.redirect a {
  color: black;
  text-decoration: none;
}

footer {
  position: absolute;
  bottom: 2%;
}
