.Empezar {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Empezar p {
  font-weight: "Poppins";
  color: #245880;
}
.Empezar .items {
  width: 30%;
  display: flex;
  justify-content: space-between;
}
.card {
  padding: 7%;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}
.Empezar form {
  margin-top: 2%;
  width: 50%;
}

.input-group-text {
  padding: 0px 5px;
  border: none;
  background: none;
}
