.Resumen {
  padding: 0% 5%;
  font-family: "Poppins";
}

.tarjeta {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  padding: 1% 0%;
}
.tarjeta .flex {
  display: flex;
  justify-content: space-evenly;
}
.tarjeta .title {
  color: #245880;
  font-size: small;
}

.tarjeta .small {
  color: #bcbcbc;
  font-size: 10px;
  position: initial;
}

.tarjeta span {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tarjeta span h3 {
  font-weight: bold;
}

.Resumen .grind {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 3%;
}
.grind .cardd {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  padding: 20px;
}
.cardd small {
  color: #bcbcbc;
}
.cardd h5 {
  font-weight: bold;
}

.cardd.Productos,
.cardd.Cupones {
  display: flex;
  flex-direction: column;
}
