.Header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Header img {
  padding: 2%;
}

.Progreso {
  display: flex;
  justify-content: space-between;
  width: 70%;
  background: url(../../../assets/Linea.png);
  background-size: contain;
  background-position-y: 17.5%;
  background-repeat: no-repeat;
}
.Progreso span {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Progreso img {
  padding: 0%;
  object-fit: contain;
}
