.Inventario {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Inventario p {
  font-weight: "Poppins";
  color: #245880;
}

.Inventario .inventario-grid {
  display: grid;
  grid-template-columns: 0.8fr 1fr 0.8fr;
  grid-gap: 0 5%;
  padding: 0% 3%;
  margin-top: 1%;
}
.inventario-grid label {
  padding: 0% 2%;
}
input[type="file"] {
  display: none;
}

.Inventario .btn-Registrarse {
  margin-top: 8%;
}
